
.slider-track {
    width: 100%;
    height: 100%;
    position: absolute; 
    bottom: 0px;
    left: 0px;
    opacity: 0.7;
}
.slider-bar {
    -webkit-transform: translateX(0px);
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.slider-label {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    width: 100%;
    font-weight: 100;
}



